import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Import Insurer Logos
import MultiCompareView from '../Templates/MultiCompareView/MultiCompareView';

import RetirementEditDetail from './RetirementEditDetail';

import { ampTag } from '../../lib/tagFunctions';

class RetirementChoosePlan extends Component {
  getAmpTags(selectedPlanData, planType) {
    try {
      return ({
        partner: selectedPlanData.info.insurer_slug,
        price: selectedPlanData.policyInfo[planType].net_price,
      });
    } catch (error) {
      console.log(`Retirement choose plan Buy now Amp tag failed: ${error.message}`);
    }

    return {};
  }

  render() {
    const { retirementFields } = this.props;

    return (
      <MultiCompareView
        {...this.props}
        category="retirement"
        form="retirement"
        editForm={{
          valuesToListen: [
            { value: 'gender', desc: 'Gender' },
            { value: 'smoker', desc: 'S.Status' },
            { value: 'birth_date', desc: 'Age' },
            { value: 'retirement_monthly_sum', desc: 'R. Sum', extra: 'cost_simplify' },
            { value: 'retirement_age', desc: 'R. Age' },
            { value: 'premium_term', desc: 'Period' },
            { value: 'payout_term', desc: 'Pay for' },
          ],
          editDetail: <RetirementEditDetail retirementFields={retirementFields} isPopUp />,
        }}
        buttonDesc="Select"
        forceFixedOnDesktop
        trackingTags={{
          default: (selectedPlanData) => {
            ampTag('RetireSelectA', this.getAmpTags(selectedPlanData, 'default'));
          },
        }}
      />
    );
  }
}

RetirementChoosePlan.defaultProps = {
};

RetirementChoosePlan.propTypes = {
  retirementFields: PropTypes.object.isRequired,
};

export default RetirementChoosePlan;
