import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { parseRetirementForm, getRetirementPlans, getValidInsurersList, clearRetirementData, setSelectedPlanData } from '../../actions/retirementActions';

import RetirementChoosePlan from '../../components/Retirement/RetirementChoosePlan';
import { gaTag, ampTag } from '../../lib/tagFunctions';

class RetirementChoosePlanContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHOOSEPLAN.index);

    gaTag({
      page_title: 'Retirement - Choose Plan',
      page_path: '/retirement/chooseplan',
    });
    ampTag('RetireResultsS');
  }

  render() {
    const {
      retirementForm,
      getPlans,
      getInsurersList,
      clearData,
      setFormState,
      plansData,
      validInsurersList,
      retirementFields,
      history,
    } = this.props;

    return (
      <RetirementChoosePlan
        mainForm={retirementForm}
        retirementFields={retirementFields}
        parseForm={parseRetirementForm}
        getPlans={getPlans}
        getInsurersList={getInsurersList}
        clearData={clearData}
        setSelectedPlanData={this.props.setSelectedPlanData}
        setFormState={() => {
          setFormState(FORMSTATES.DETAILFORM.index);
          history.push(`/retirement${FORMSTATES.DETAILFORM.path}`);
        }}
        loadingFailBackState={() => {
          history.goBack();
        }}
        plansData={plansData}
        validInsurersList={validInsurersList}
      />
    );
  }
}

RetirementChoosePlanContainer.defaultProps = {
  plansData: null,
  validInsurersList: null,
};

RetirementChoosePlanContainer.propTypes = {
  retirementForm: PropTypes.object.isRequired,
  getPlans: PropTypes.func.isRequired,
  getInsurersList: PropTypes.func.isRequired,
  clearData: PropTypes.func.isRequired,
  setSelectedPlanData: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  plansData: PropTypes.array,
  validInsurersList: PropTypes.array,
  retirementFields: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  retirementForm: getFormValues('retirement')(state),
  retirementFields: state.retirement.retirementFields,
  plansData: state.retirement.plansData,
  validInsurersList: state.retirement.validInsurersList,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  getPlans: (parsedForm) => {
    dispatch(getRetirementPlans(parsedForm));
  },
  getInsurersList: (parsedForm) => {
    dispatch(getValidInsurersList(parsedForm));
  },
  clearData: () => {
    dispatch(clearRetirementData());
  },
  setSelectedPlanData: (data) => {
    dispatch(setSelectedPlanData(data));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(RetirementChoosePlanContainer));
